.wrap{
    margin-top: -2px;
    margin-bottom: -2px;
}

.tr{
    width: 1200px;
    display: flex;
    justify-content: space-between;
    background-color: #111111;
}

.tr>td{ display: flex;
    justify-content: center;
    align-items: center;
    width: 168px;
    margin-top: 3px;
    background-color: #222222;
    padding: 7px 0;
    color: rgb(151, 149, 147);
    cursor: pointer; 
    font-size: 14px;

}
.tr>td:hover{ 
    background-color: rgb(65, 65, 63);
}